/* ========== DEPENDENCIES ============= */
import React, { useState } from "react";
import Modal from "react-modal";
/* ============ CODE ============== */
export const BookInfo = ({
  openInfoModal,
  closeInfoModal,
  bookId,
  bookTitle,
  authorsName,
  yearOfBirth,
  yearOfDeath,
}) => {
  const [bookInfo, setBookIndo] = useState({
    authorsName: authorsName,
    yearOfBirth: yearOfBirth,
    yearOfDeath: yearOfDeath,
  });
  const onChange = (e) => {
    setBookIndo({
      ...bookInfo,
      [e.target.name]: e.target.value,
    });
  };
  return (
    <Modal
      isOpen={openInfoModal}
      onRequestClose={closeInfoModal}
      contentLabel={bookTitle}
      closeTimeoutMS={800}
      className="info-modal"
      key={bookId}
      style={{
        overlay: {
          backgroundColor: "rgba(0, 0, 0, .75)",
        },
        content: {
          color: "lightsteelblue",
        },
      }}
      ariaHideApp={false}>
      <article className="book-info">
        <div className="book-info__topbar">
          <h3 className="book-info__heading">Book Info</h3>
          <button className="book-info__close-btn" onClick={closeInfoModal}>
            X
          </button>
        </div>
        <form className="book-info__form">
          <fieldset className="book-info__fieldset">
            <legend className="book-info__legend">Author Details</legend>
            <label className="book-info__label">
              Author:
              <input
                className="book-info__input"
                type="text"
                name="authorsName"
                value={bookInfo.authorsName}
                onChange={onChange}
              />{" "}
            </label>
            <label className="book-info__label">
              Year of Birth:
              <input
                className="book-info__input"
                type="text"
                name="yearOfBirth"
                value={bookInfo.yearOfBirth}
                onChange={onChange}
              />{" "}
            </label>
            <label className="book-info__label">
              Year of Death:
              <input
                className="book-info__input"
                type="text"
                name="yearOfDeath"
                value={bookInfo.yearOfDeath}
                onChange={onChange}
              />{" "}
            </label>
          </fieldset>
        </form>
      </article>
    </Modal>
  );
};
